import { useSelector } from "react-redux";

// Components
import { UserPhoto } from "../UserPhoto";
import styles from "./RightSide.module.scss";
import photo from "../../img/userIcon.png";
import { TypographyMain } from "../TypographyMain";

const RightSide = () => {
  const { userData } = useSelector((state) => state.userDB);
  return (
    <>
      <div className={styles.rightSide}>
        <div className={styles.userPanel}>
          <button />
          <div className={styles.headerUserPanel}>
            <UserPhoto size="l" src={photo} />
            {/* {userData && (
              <TypographyMain
                text={`${userData.firstName} ${userData.lastName}`}
                second="tampoox@gmail.com"
                size="l"
              />
            )} */}

            {/* {userData && (
              <div className={styles.wrapper}>
                <span
                  className={styles.lText}
                >{`${userData.firstName} ${userData.lastName}`}</span>
                <span className={styles.lSecond}>{userData.email}</span>
              </div>
            )} */}

            {userData && (
              <div className={styles.wrapper}>
                <input
                  disabled
                  maxLength="18"
                  type="text"
                  className={styles.name}
                  defaultValue={`${userData.firstName} ${userData.lastName}`}
                />
                <input
                  disabled
                  type="text"
                  className={styles.email}
                  defaultValue={userData.email}
                />
              </div>
            )}
          </div>
          <hr />
          <div className={styles.mainUserPanel}>
            <label>
              <span>Отдел:</span>
              <input type="text" disabled value="Маркетинг" />
            </label>

            <label>
              <span>Отдел:</span>
              <input type="text" />
            </label>

            <label>
              <span>Отдел:</span>
              <input type="text" />
            </label>

            <label>
              <span>Отдел:</span>
              <input type="text" />
            </label>
          </div>
        </div>
      </div>
    </>
  );
};

export { RightSide };
