import { useState, useEffect } from "react";

// Store
import { useDispatch, useSelector } from "react-redux";
import { userDBAction } from "../store/slice/userDBSlice";

// Firebase
import { ref, onValue, set } from "firebase/database";
import { auth, db } from "../firebase";

const useUserDB = () => {
  const [response, setResponse] = useState();

  const dispatch = useDispatch();
  const { firstName, lastName, email, phoneNumber } = useSelector(
    (state) => state.registration
  );

  useEffect(() => {
    auth.onAuthStateChanged((user) => {
      if (user) {
        onValue(ref(db, `/users`), (snapshot) => {
          const data = snapshot.val();

          setResponse(data);
        });
      }
    });
  }, [dispatch]);

  useEffect(() => {
    if (response !== undefined) {
      const creatUserDB = () => {
        set(ref(db, `/users/${auth.currentUser.uid}`), {
          firstName,
          lastName,
          email,
          phoneNumber,
          department: "",
          position: "",
          uid: auth.currentUser.uid,
        });
      };

      if (response === null) {
        creatUserDB();
        return;
      }

      const user = response[auth.currentUser.uid];

      if (user === undefined) {
        creatUserDB();
        return;
      }

      dispatch(userDBAction.setUserData(user));

      dispatch(userDBAction.setUsersData(response));
    }
  }, [response]);
};

export { useUserDB };

